 
import {Routes, RouterModule} from '@angular/router';
import {NgModule, Optional, SkipSelf, ModuleWithProviders} from '@angular/core'; 

const appRoutes: Routes = [

    {    path: '', pathMatch: 'full', redirectTo: '/login' },
    {
        path: 'login',
        loadChildren: 'app/account/account.module#AccountModule'
    },
    {
        path: 'home',
        loadChildren: 'app/home/home.module#HomeModule'
    },
    {
        path: 'product',
        loadChildren: 'app/products/products.module#ProductsModule'
    },
    {
        path: 'project',
        loadChildren: 'app/projects/projects.module#ProjectsModule'
    },
    {
        path: 'quote',
        loadChildren: 'app/quotes/quotes.module#QuotesModule'
    },
    {
        path: 'order',
        loadChildren: 'app/orders/orders.module#OrdersModule'
    },
    {
        path: 'discountRequest',
        loadChildren: 'app/discount/discount.module#DiscountRequestModule'
    },
    {
        path: 'commissionRequest',
        loadChildren: 'app/commission/commission.module#CommissionRequestModule'
    },
    {
        path: 'tools',
        loadChildren: 'app/tools/tools.module#ToolsModule'
    },
    {
        path: 'submittalPackage',
        loadChildren: 'app/submittal/submittal.module#SubmittalPackageModule'
    },
    //{
    //    path: 'systemConfigurator',
    //    loadChildren: 'app/system-configurator/system-configurator.module#SystemConfiguratorModule'
    //}
];

@NgModule({
  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule],
})
export class AppRoutingModule {
  constructor(@Optional() @SkipSelf() parentModule: AppRoutingModule) {
    if (parentModule) {
      throw new Error(
        'AppRoutingModule is already loaded. Import it in the AppModule only');
    }
  }

  static forRoot(): ModuleWithProviders {
    return {
      ngModule: AppRoutingModule,
      providers: []
    };
  }
}



